import { transferAccount } from "jcc_exchange";
import { JcWalletTool } from "jcc_wallet";
import { jNodeConfig } from "jcc_mixins";
import { Toast } from "vant";
import { mapGetters } from "lib";
import { isEmpty } from "js/utils";
import Lockr from "lockr";
export default {
  mixins: [jNodeConfig],
  data() {
    return {
      contact: {
        name: "",
        address: ""
      },
      loading: false,
    }
  },
  computed: {
    ...mapGetters(["swtAddress", "jcWallet"])
  },
  methods: {
    getBaseName(base) {
      if (base === "CNT") {
        return "CNY";
      } else if (base === "SWTC") {
        return "SWT";
      } else {
        return base;
      }
    },
    orderWithtradePsw() {
      console.log("port=" + this.port)
      let jcTool = new JcWalletTool(this.$store.getters.jcWallet);
      let proEnv = process.env.NODE_ENV === "production";
      let hosts = proEnv ? this.exHosts : [window.location.hostname];
      let port = proEnv ? this.exPort : "8080";
      jcTool
        .validatePassword(this.form.tradePsw, "swt")
        .then(secret => {
          var data = {};
          data.issuer = process.env.currencies.UST.issuer;
          data.currency = this.getBaseName(this.form.currency.name);
          data.address = this.swtAddress;
          data.to = this.form.walletAddress;
          data.amount = this.form.AmountCollected;
          data.memo = this.form.remark;
          data.secret = secret;
          data.hosts = hosts;
          data.port = port;
          data.https = this.https;
          setTimeout(() => {
            this.confirmPay(data);
          }, 1000);
        })
        .catch(error => {
          Toast.fail(error.message);
        })
        .finally(() => {});
    },
    confirmPay(data) {
      console.log("开始转账");
      console.log(data);
      let contactArr = Lockr.get("saveContact");
      console.log(contactArr);
      if (!Array.isArray(contactArr) || isEmpty(contactArr)) {
        contactArr = [];
      }
      this.contact.name = this.form.payeeName;
      this.contact.address = this.form.walletAddress;
      transferAccount(data)
        .then(() => {
          let item = contactArr.find(item => {
            return this.form.walletAddress === item.address;
          });
          if (!item) {
            contactArr.push(this.contact);
            Lockr.set("saveContact", contactArr);
          }
          Toast.success(this.$t("message.transferPayment.payments.transferSuccess"));
        })
        .catch(error => {
          console.log(error.message);
          Toast.fail(error.message);
        });
    }
  }
}


