<template>
  <div id="payment">
    <section style="padding:1.28rem 0.3rem 0 0.3rem">
      <ol class="inputInformation">
        <li>
          <!-- Input Payee Name -->
          <div style="position:relative;width:100%;margin-right:0.15rem;">
            <input v-model="form.payeeName" type="text" :placeholder="$t('message.transferPayment.payments.payee')">
            <!-- Contacts icon -->
            <i class="contacts_icon positionIcon" style="margin-top: -0.15rem;" @click="toContact"></i>
          </div>
          <!-- Scaning icon -->
          <i class="scanning_icon"></i>
        </li>
        <li>
          <input v-model="form.walletAddress" type="text" :placeholder="$t('message.transferPayment.payments.walletAddress')">
        </li>
        <li>
          <!-- Input Transfer Amount -->
          <input v-model="form.AmountCollected" type="number" :placeholder="$t('message.transferPayment.payments.receivableAmount')" style="width:100%">
          <!-- Currency Selector -->
          <div style="position:relative;min-width:2.2rem;margin-left:0.2rem;">
            <i :class="`currency-icon-${form.currency.name}`" style="position:absolute;top:0.17rem;left:50%;margin-left:-0.7rem;"></i>
            <input v-model="form.currency.name" @click="form.currency.clicked = !form.currency.clicked;"  class="placeholder" style="text-align:center;text-indent:0.15rem;" readonly="readonly" :placeholder="$t('message.transferPayment.payments.currency')">
            <i :class="`triangle_${form.currency.clicked?'clicked':'default'}`" style="position:absolute;top:0.26rem;right:0.2rem;"></i>
            <JcSelect v-show="form.currency.clicked" v-model="form.currency.name" :data="currencyList" @close="form.currency.clicked=false;" :align="'left'" :width="'100%'" :borderColor="'#559df7'" :itemSpacing="'0.15rem'" :indent="'0.4rem'" :fontSize="'0.30rem'" style="position:absolute;top:100%;left:50%;margin-left:-1.1rem;margin-top:0.2rem;"></JcSelect>  
          </div>
        </li>
        <li>
          <!-- Input Remark -->
          <textarea v-model="form.remark" rows="2" cols="300" :placeholder="$t('message.transferPayment.payments.note')" maxlength="50" style="height:1.8rem;padding:0.22rem 0 0 0"></textarea> 
        </li>
        <li>
          <!-- Confirm Button -->
          <div class="confirm" @click="showConfirmDialog = true">{{$t('message.transferPayment.transactionInquiry.confirm')}}</div>
        </li>
      </ol>
      <van-dialog v-model="showConfirmDialog" :title="$t('message.transferPayment.payments.dialogTitle')" center :show-cancel-button="false" :show-confirm-button="false" style="margin-top:-1rem;margin-top:-20%;">
        <ul class="dialogForm">
          <li style="justify-content:flex-start;">{{$t('message.transferPayment.payments.inputPassword')}}</li>
          <li><input v-model="form.tradePsw" type="password" style="text-indent:0.2rem;"></li>
          <div style="height:0.1rem;"></div>
          <li><button @click="confirmTransfer" style="background:#1D93F7">{{$t('message.transferPayment.payments.confirm')}}</button></li>
        </ul>
      </van-dialog>
    </section>
  </div>
</template>
<script>
import JcSelect from "@/components/jc_select";
import transfer from "@/mixins/transfer";
import { Toast } from "vant";

export default {
  name: "payment",
  components: { JcSelect },
  mixins: [transfer],
  mounted() {
    setTimeout(() => {
      this.handleCoinList();
    }, 300);
  },
  props: {
    userName: {
      type: String,
      default: ""
    },
    address: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      currencyList: [],
      showConfirmDialog: false,
      form: {
        payeeName: "",
        walletAddress: "",
        AmountCollected: "",
        remark: "",
        currency: { name: "", clicked: false },
        tradePsw: ""
      }
    };
  },

  created() {
    this.form.payeeName = this.userName;
    this.form.walletAddress = this.address;
  },
  computed: {
    getCoinList() {
      return this.$store.getters.coins;
    },
    checkForm() {
      return (
        this.form.payeeName !== "" &&
        this.form.walletAddress !== "" &&
        this.form.AmountCollected !== "" &&
        this.form.currency.name !== ""
      );
    }
  },
  methods: {
    toContact() {
      this.$router.push({
        path: "/contact",
        query: {
          currentShow: "payment"
        }
      });
    },
    handleCoinList() {
      this.getCoinList.forEach(el => {
        this.currencyList.push({
          name: el.label,
          icon: `currency-icon-${el.label}`
        });
      });
    },
    confirmTransfer() {
      if (!this.checkForm || this.loading) {
        this.showConfirmDialog = false;
        this.form.tradePsw = "";
        return;
      }
      this.showConfirmDialog = false;
      this.loading = true;
      Toast.loading({
        duration: 0,
        forbidClick: true,
        message: this.$t("message.transferPayment.payments.transfering")
      });
      this.orderWithtradePsw();
    }
  }
};
</script>
<style lang="scss" scoped>
.inputInformation {
  width: 100%;
  font-size: 0.26rem;
  color: #4d4d4d;
  li {
    width: 100%;
    display: flex;
    align-items: center;
    margin: 0.2rem 0;
    label {
      min-width: 1.3rem;
      max-width: 1.5rem;
      text-align: left;
    }
    input,
    textarea {
      width: 100%;
      height: 0.7rem;
      border: 0.01rem solid #b1bec6;
      border-radius: 0.1rem;
      text-indent: 0.2rem;
      font-size: 0.3rem;
      &:focus {
        border: 0.01rem solid #559df7;
      }
    }
    .positionIcon {
      position: absolute;
      top: 50%;
      right: 0;
      margin-right: 0.2rem;
    }
  }
}
.confirm {
  width: 100%;
  height: 0.76rem;
  background-color: #3f7bff;
  line-height: 0.76rem;
  color: #fff;
  font-size: 0.26rem;
  border-radius: 0.1rem;
  margin-top: 0.4rem;
}
.placeholder::-webkit-input-placeholder {
  text-align: left;
}
.dialogForm {
  width: 100%;
  color: #fff;
  font-size: 0.26rem;
  li {
    width: 100%;
    height: 0.7rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0.2rem 0;
  }
  input {
    background: #424b68;
  }
  input,
  button {
    display: inline-block;
    width: 100%;
    height: 100%;
    border-radius: 0.08rem;
    border: none;
  }
}
</style>


